// Como son varias funciones se resgistran en el EventListener y se llaman por medio de otra función
document.addEventListener('DOMContentLoaded', function (){
    iniciarApp(); // Iniciar App y aligerar esta funcion
});

//Llamar las funciones
function iniciarApp(){
    navegacionFija(); // Funcion de Navegacion
    crearGaleria(); // Funcion de galeria
    scrollNav(); // Funcion de scroll
};

// Navegacion
function navegacionFija(){
    const barra = document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre-festival');
    const body = document.querySelector('body');

    window.addEventListener('scroll', function(){ // Evento de scroll en la venta de window
        if(sobreFestival.getBoundingClientRect().bottom<0){ // Resgistra los datos al dar scroll
            barra.classList.add('fijar-barra');
            body.classList.add('body-scroll');
        } else{
            barra.classList.remove('fijar-barra');
            body.classList.remove('body-scroll');
        }
    });
};

// Scroll
function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal a'); // Seleccionar todos los enlaces
    enlaces.forEach(enlace => {
        enlace.addEventListener('click', function(e){
            e.preventDefault(); // Evitar comportamiento por default
            // Añadir nuevo comportamiento
            // console.log(e.target.attributes.href.value);
            // .target registra lo que le hemos dado click
            // .attributes acceder a los atributos
            // .href accedo al href
            // .value accedo al valor del href
            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            seccion.scrollIntoView({ behavior: "smooth"}); // API para el scroll y añadimos nuevo comportamiento
        });
    });
};

// Galeria
function crearGaleria(){
    const galeria = document.querySelector('.galeria-imagenes');
        for(let i=1; i <= 12; i++){
            // Creamos un picture para mostrar la galeria
            const imagen = document.createElement('picture');
            // Utilizamos innerHTML para establecer sintaxis HTML describiendo la descendiente del elemento 
            imagen.innerHTML = `
                <source srcset="build/img/thumb/${i}.avif" type="image/avif">
                <source srcset="build/img/thumb/${i}.webp" type="image/webp">
                <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen vocalista">
            `;
            // Evento al dar click
            imagen.onclick = function(){ // Evento al dar click
                mostrarImagen(i); // Lo hacemos como CallBack dado que debemos identificar la imagen a mostrar
            };
            // Añadir elemento HTML creado con JS
            galeria.appendChild(imagen); 
        };
};

// Mostrar Imagen Grande
function mostrarImagen(id){
    // Creamos un picture para la imagen en tamaño grande a mostrar
    const imagen = document.createElement('picture'); 
        imagen.innerHTML = `
            <source srcset="build/img/grande/${id}.avif" type="image/avif">
            <source srcset="build/img/grande/${id}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen vocalista">
        `;
    // Crear el Overlay
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');
    overlay.onclick = function(){
        const body = document.querySelector('body');
        body.classList.remove('fijar'); // Remover la clase de fijar pantalla
        overlay.remove(); // Remover el overlay o cerrar la imagen
    };
    // Boton para cerrar imagen
    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent = 'X';
    cerrarImagen.classList.add('boton-cerrar');
    cerrarImagen.onclick = function(){
        const body = document.querySelector('body');
        body.classList.remove('fijar');
        overlay.remove();
    }
    overlay.appendChild(cerrarImagen);
    // Añadiendo al HTML
    const body = document.querySelector('body'); 
    body.appendChild(overlay); // Añadir al HTML
    body.classList.add('fijar');
}